import { Link } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useProfiles from '../../../hooks/profiles/useProfiles';
import PageMeta from '../../Layout/PageMeta/PageMeta';

const ProfileOverview = () => {
  const { data, isLoading } = useProfiles();

  if (isLoading) {
    return <>Loading</>;
  }

  return (
    <section>
      <PageMeta title={t('My Data')} />
      <div className="-m-2 flex flex-row">
        {data?.definitions.map((definition) => {
          return (
            <div className="w-1/4 p-2" key={definition.name}>
              <Link to={definition.slug}>
                <div className="border border-slate-50 bg-slate-200 p-2 hover:bg-slate-300 dark:border-slate-600 dark:bg-slate-700 dark:text-white">
                  <h2 className="text-xl">{definition.name}</h2>
                  <p>{definition.description}</p>
                </div>
              </Link>
            </div>
          );
        })}
      </div>
    </section>
  );
};

export default ProfileOverview;
