import { useQuery } from '@tanstack/react-query';
import { ApiType, Client, ProfileDefinition } from '@youfoundation/transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';

interface ProfileDefinitionVm extends ProfileDefinition {
  slug: string;
}

const useProfiles = () => {
  const { getSharedSecret } = useAuth();
  const fetchData = async () => {
    const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

    const definitions = await (
      await client.profileDefinitionProvider.getProfileDefinitions()
    ).map((def) => {
      return {
        ...def,
        slug: convertTextToSlug(def.name),
      } as ProfileDefinitionVm;
    });

    return { definitions };
  };

  return useQuery(['profiles'], () => fetchData(), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
  });
};

export default useProfiles;
